import { TextField, Box, Button, Grid } from "@mui/material";
import HelpOutlineOutlinedIcon from "@mui/icons-material/HelpOutlineOutlined";

import classes from "./InputSalaryPage.module.css";
import { SetStateAction, useState, useEffect } from "react";
import { Salary } from "../model/SalaryModel";
import SalaryTable from "../components/SalaryTable";
import CurrencyInput from "react-currency-input-field";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { ko } from "date-fns/esm/locale";
import {
  getSalaryData,
  saveSalaryData,
  resetSalaryData,
} from "../store/localstorageUtil";
import CustomTextField from "../components/CustomTextField";

const numberWithCommas = (x: string) => {
  x = x.replaceAll(",", "");
  x = x.replace(/[^0-9]+$/g, "");
  return x.replace(/\B(?=(\d{3})+(?!\d))/g, ",");
};

const InputSalaryPage = () => {
  const [salaryList, setSalaryList] = useState<Salary[]>([]);
  const [currentInputValue, setCurrentInputValue] = useState<string>("");
  const [startDate, setStartDate] = useState<Date | null>();

  useEffect(() => {
    const stoageData = getSalaryData();
    if (stoageData) {
      setSalaryList(stoageData);
    }
  }, []);

  const calculateRiseRate = (currSalaryList: Salary[]) => {
    let newSalaryList: Salary[] = [];
    currSalaryList.forEach((el, idx) => {
      const salary: Salary = {
        ...el,
      };
      if (idx > 0) {
        const prevNum = currSalaryList[idx - 1].amount;
        const nextNum = salary.amount;
        salary.riseRate = ((nextNum - prevNum) / prevNum) * 100;
      } else {
        salary.riseRate = undefined;
      }
      newSalaryList.push(salary);
    });
    setSalaryList(newSalaryList);
    saveSalaryData(newSalaryList);
  };

  const addData = () => {
    if (!currentInputValue) {
      return;
    }

    const stringData = startDate
      ? `${startDate.getFullYear()} ${startDate.getMonth() + 1}`
      : "";

    calculateRiseRate([
      ...salaryList,
      {
        id: Date.now(),
        date: stringData,
        amount: parseInt(currentInputValue.replaceAll(",", "")),
      },
    ]);
    setCurrentInputValue("");
    setStartDate(null);
  };

  const deleteItem = (itemId: number) => {
    const newList = salaryList.filter((el) => el.id !== itemId);
    calculateRiseRate(newList);
  };

  return (
    <>
      <Grid className={classes.row} container spacing={4}>
        <Grid item>
          <div>
            <DatePicker
              placeholderText="  "
              selected={startDate}
              className={classes.datePicker}
              onChange={(date) => setStartDate(date)}
              dateFormat="yyyy MM"
              showMonthYearPicker
              clearButtonTitle=""
              customInput={<TextField />}
              locale={ko}
            />
          </div>
        </Grid>
        <Grid item>
          <TextField
            placeholder=" *"
            value={currentInputValue}
            onChange={(e) =>
              setCurrentInputValue(numberWithCommas(e.target.value) ?? "")
            }
          />
        </Grid>
        <Grid item>
          <Button
            variant="outlined"
            size="large"
            color="primary"
            onClick={addData}
            sx={{ margin: "0 8px" }}
            fullWidth
          >
            
          </Button>
        </Grid>
      </Grid>
      <Box className={classes.infoText}>
        <HelpOutlineOutlinedIcon />
        <span>  .</span>
      </Box>
      <SalaryTable salaryList={salaryList} deleteItem={deleteItem} />
      <Box>
        <Button
          variant="outlined"
          size="large"
          onClick={() => {
            setSalaryList([]);
            resetSalaryData();
          }}
          sx={{margin: "2rem 0" }}
          color="warning"
        >
          RESET
        </Button>
      </Box>
    </>
  );
};

export default InputSalaryPage;
